// common/http.interceptor.js

import config from '@/common/config.js';

const install = (Vue, vm) => {
	
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: config.baseUrl,
		loadingText: '~',
		loadingTime: 1000,
		originalData:true,
		// content-type
		// header: {
		// 	'content-type': 'xxx'
		// }
		// ......
	});

	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;

		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;

		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;

		// tokenStorage
		// Storage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		config.header.Authorization = vm.$store.state.token
		// urlurlthis.$u.get(url)url
		if (config.url == '/auth/wechatLogin') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		
		// 
		if(res.statusCode == 200){
				return res.data;
		}else{
			if(res.statusCode == 401){
				uni.removeStorageSync('navUrl')
				uni.removeStorageSync('userInfo')
				uni.removeStorageSync('token')
				Vue.prototype.$u.vuex('token', '')
				uni.redirectTo({
					url: '/pages/login/login',
				})
			}else{
				vm.$u.toast('!');
			}
		}
		
		return false;
	}
}

export default {
	install
}
